import coutryCardTpl from '../templates/country-card.hbs';

fetch('https://restcountries.eu/rest/v2/name/switzerland')
    .then(response => {
        return response.json();
    })
    .then(countryName => {
        console.log(countryName);
        const markup = coutryCardTpl(countryName);
        console.log(markup);
    })
    .catch(err => {
        console.log(err);
    });